/* Style for the chat message container */
.chat-massage-box{
    position: relative;
    max-width: 90%; /* Set your desired maximum width */
    background-color: #007bff; /* Background color for the message bubble */
    color: #fff; /* Text color */
    padding: 10px 15px; /* Padding inside the message bubble */
    margin-bottom: 20px; /* Spacing between messages */
    border-radius: 10px; /* Rounded corners for the message bubble */
  }
  
