.container{
    background-color: #282c34;
    width: 100%;
  min-height: 100vh;
  display: flex;
 
  
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  

}
.main{
    display: flex;
    justify-content: center;
    background-color: azure;
    width: 70%;
    height: 70vh;
    border-radius: 10px;
    color: black;
    
}

.left_part{
    width: 40%;
    display: flex;
    flex-direction: column;
    border-right: 2px solid;
    margin: 10px;
    
    
    
    
}
.search_bar{
    
    >input {
        width: 92%;
        border-radius: 5px;
    }
}
.post_lists{
    overflow: scroll;
  scrollbar-color: red orange;
  scrollbar-width: thin;

}

.right_part{
    display: none;
    width: 60%;
    display: flex;
    flex-direction: column;
    
    margin-top: 30px;
    overflow: scroll;
  scrollbar-color: red orange;
  scrollbar-width: thin;
    
    
    
    
}





.post_list{
    display: flex;
    border: 1px solid;
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    background-color:white;
    
    align-items: center;
}
.post_list:hover{
    background-color: gray;
    color: white;
    cursor: pointer;
    
}
.id{
    font-weight: bold;
}
.post_name{
    margin-left: 10px;
}